<template>
    <div class="main">
        <div class="multimediaManagement">
            <div class="searchA">
                <el-input v-model="keyword" placeholder="播放名称" clearable size="small"></el-input>
                <el-button size="small" type="primary" plain icon="el-icon-search" @click="serchList()">查询</el-button>
                <div class="add">
                    <el-button size="small" type="primary" icon="el-icon-plus" @click="addBtn">新增播放</el-button>
                </div>
            </div>
            <div class="home_table">
                <el-table :data="tableData" border v-loading="isLoading" element-loading-background="rgba(0, 0, 0, 0.4)" :max-height="tableHeight" size="medium" style="width: 100%">
                    <template v-for="(item, index) in tableHead">
                        <el-table-column :prop="item.prop" :label="item.title" :key="index" :min-width="item.minWidth" :show-overflow-tooltip="true"></el-table-column>
                    </template>
                    <el-table-column prop="address" label="操作" fixed="right" width="240">
                        <template slot-scope="scope">
                            <el-button @click="stop(scope.row,1)" v-if="scope.row.status==0" type="text" size="small">开始播放</el-button>
                            <el-button @click="stop(scope.row,0)" v-else type="text" size="small" style="color: #ff2b2b">停止播放</el-button>
                            <el-button @click="editBtn(scope.row)" v-if="scope.row.status==0&&scope.row.file_source==1" type="text" size="small">编辑</el-button>
                            <el-button @click="delBtn(scope.row.id)" v-if="scope.row.status==0" type="text" size="small" style="color: #ff2b2b">删除</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
            <div class="pagination">
                <el-pagination background :current-page.sync="page" :page-sizes="[15, 30, 50, 100]" :page-size="pageSize" layout="prev, pager, next, sizes,total" :total="total" class="pagination" @size-change="handleSizeChange" @current-change="handleCurrentChange" :hide-on-single-page="total<15? true:false" />
            </div>
        </div>
    </div>
</template>
<script>
import {
    multimediaList, multimediaView, multimediaInfo, multimediaAdd, multimediaEdit, multimediaDel, multimediaStop, hashApi,
} from '@/api/index';
export default {
    data() {
        return {
            total: 0,
            page: 1,
            pageSize: 15,
            mhVal: '',
            isLoading: false,
            tableHeight: window.innerHeight - 180,
            formData: {
                selectVal3: '',
                dialogNameVal: '',
                content: ''
            },
            title: '',
            dialogVisible: false,
            rule: {
                dialogNameVal: [
                    { required: true, message: '请输入标题', trigger: 'blur' }
                    // { min: 1, max: 160, message: '长度在 1 到 160 个字符', trigger: 'blur' },
                ],
                selectVal3: [
                    { required: true, message: '请选择类型', trigger: 'change' }
                ],
                // selectVal4: [
                //     { required: true, message: '请选择发布机构', trigger: 'change' },
                // ],
                content: [
                    { required: true, message: '请输入文章内容', trigger: 'blur' }
                ]
            },
            tableHead: [
                { title: '序号', prop: 'id', minWidth: '100' },
                { title: '播放名称', prop: 'screen_name', minWidth: '160' },
                { title: '播放类型', prop: 'play_type_name', minWidth: '100' },
                { title: '设备类型', prop: 'screen_type_name', minWidth: '100' },
                { title: '设备选择', prop: 'device_name', minWidth: '160' },
                { title: '素材类型', prop: 'show_type_name', minWidth: '100' },
                { title: '播放次数', prop: 'frequency', minWidth: '100' }
            ],
            tableData: [],
            hash: '',
            listId: '',
            resources: {}, // 权限
            grade_auth: false, // 市局权限
            method_auth: {
                actAdd: 0, // 政策公告新增
                actEdit: 0, // 政策公告编辑
                actDelete: 0, // 政策公告删除
                getDepartmentList: 0 // 政策发布机构-未用
            },
            keyword: '',
        };
    },
    mounted() {
        this.getList();
        this.getHash();
    },
    methods: {
        serchList() {
            this.page = 1;
            this.pageSize = 15;
            this.getList()
        },
        // 监听 pagesize 改变
        handleSizeChange(newSize) {
            this.page = 1;
            this.pageSize = newSize;
            this.getList();
        },
        // 监听 pagenum 改变
        handleCurrentChange(newPage) {
            this.page = newPage;
            this.getList();
        },

        //获取Hash
        async getHash() {
            const res = await this.axios.post(hashApi, { page_hash: 'multimediaManagement' });
            if (res.data.code == 1) {
                this.hash = res.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        async getList() {
            let data = {
                curr: this.page,
                pageSize: this.pageSize,
                keyword: this.keyword,
            }
            const res = await this.axios.post(multimediaList, data);
            if (res.data.code == 1) {
                this.total = res.data.data.total;
                this.tableData = res.data.data.data;
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
        },
        //新增播放
        addBtn() {
            this.$router.push({ name: "multimediAdd" })
        },
        editBtn(row) {
            this.$router.push({
                name: 'multimediAdd', params: {
                    id: row.id,
                }
            })
        },
        //关闭弹窗
        handleClose() {
            this.dialogVisible = false;
            this.resetForm(); //重置表单
            // Object.assign(this.$data, this.$options.data())
        },
        //开始播放-停止播放
        async stop(row, status) {
            const res = await this.axios.post(multimediaStop, { id: row.id, state: status, hash: this.hash });
            if (res.data.code == 1) {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'success'
                });
            } else {
                this.$message({
                    showClose: true,
                    message: res.data.msg,
                    type: 'error'
                });
            }
            this.getList();
            this.getHash();
        },
        delBtn(id) {
            this.$confirm('是否确定删除该播放', '删除提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            })
                .then(() => {
                    const datas = {
                        id: id,
                    };
                    const res = this.axios.post(multimediaDel, datas).then(res => {
                        if (res.data.code == 1) {
                            this.$notify.success({
                                showClose: true,
                                message: res.data.msg,
                                type: 'success'
                            });
                            if ((this.total - 1) % this.pageSize === 0 && this.total > 1) {
                                this.page -= 1;
                            }
                            this.getList();
                        } else {
                            this.$notify.error({
                                title: '失败',
                                message: res.data.msg
                            });
                        }
                    });
                    this.getHash();
                })
                .catch(err => {
                    this.$notify.error({
                        title: '失败',
                        message: '取消操作'
                    });
                });
        },
    }
};
</script>

<style lang="scss" scoped>
/deep/ .multimediaManagement .ql-container .ql-editor {
    height: 280px;
}
.multimediaManagement {
    .ql-editor {
        padding: 0;
    }
    .searchA {
        display: flex;
        padding: 12px 25px;
        background: #fff;
        .el-select {
            margin-right: 24px;
        }
        .el-input {
            width: auto;
            margin-right: 24px;
        }
        .add {
            flex: 1;
            text-align: right;
        }
    }

    .dialogInput {
        display: flex;
        margin-bottom: 0px;
        .padd {
            width: 100%;
            margin-right: 20px;
            .paddTit {
                margin-bottom: 10px;
            }
        }
    }
    .btn {
        width: 250px;
    }
}
</style>



